import {Component, Input, Output, EventEmitter } from '@angular/core';
import { FormControl } from '@angular/forms';
// import { DatePickerSingleOptions } from './date-picker-single-options';
import { SingleCalendarOptions } from '../single-calendar/single-calendar-options';
import { ChangeChosenDayResponse } from '../single-calendar/single-calendar.component';
import { ChangeMonthResponse } from '../abstract-calendar/abstract-calendar.component';
import { FormatterFromDateFunction, FormatterToDateFunction } from '../../service/date-picker.service';

@Component({
  selector: 'app-date-picker-single',
  template: `<div
  class="date-picker-single-wrap"
  [class.hidden]="!isOpen()">
  <div class="date-picker-single-content">
    <app-single-calendar
      [bindFormControl]="bindFormControl"
      [noChoose]="options.noChoose"
      [startChosenToday]="options.startChosenToday"
      [startViewportAtChosen]="options.startViewportAtChosen"
      [startViewportDate]="options.startViewportDate"
      [monthLabels]="options.monthLabels"
      [dayLabels]="options.dayLabels"
      [disableDatesBefore]="options.disableDatesBefore"
      [disableDatesAfter]="options.disableDatesAfter"
      [formatterToDate]="formatterToDate"
      [formatterFromDate]="formatterFromDate"
      (changeChosenDay)="onChangeChosenDay($event)"
      (changeMonth)="onChangeMonth($event)">
    </app-single-calendar>
  </div>
</div>
`,
  styles: [`:host {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: absolute;
  z-index: 1; }

div.date-picker-single-wrap {
  display: inline-block;
  background-color: white;
  overflow: hidden;
  -webkit-transition: 200ms;
  transition: 200ms;
  max-height: 300px; }
  div.date-picker-single-wrap.hidden {
    max-height: 0; }
  div.date-picker-single-wrap div.date-picker-single-content {
    border: solid 1px grey;
    padding: 15px; }
`]
})
export class DatePickerSingleComponent {
  // weird error on typescript if i use DatePickerSingleOptions
  @Input() options = {} as SingleCalendarOptions;
  @Input() bindFormControl = new FormControl();
  @Input() formatterToDate: string | FormatterToDateFunction;
  @Input() formatterFromDate: string | FormatterFromDateFunction;
  @Output() changeMonth = new EventEmitter<ChangeMonthResponse>();
  @Output() changeChosenDay = new EventEmitter<ChangeChosenDayResponse>();
  private open = false;

  constructor() { }

  public onChangeChosenDay(changeChosenDayResponse: ChangeChosenDayResponse): void {
    this.changeChosenDay.emit(changeChosenDayResponse);
  }

  public onChangeMonth(changeMonthResponse: ChangeMonthResponse): void {
    this.changeMonth.emit(changeMonthResponse);
  }

  public toggle() {
    this.open = !this.open;
  }

  public show() {
    this.open = true;
  }

  public hide() {
    this.open = false;
  }

  public isOpen(): boolean {
    return this.open;
  }
}
